import { Inline, Text } from '@ali/4ever-cangjie';
import { Action } from '../../types';
import type {
  CardInfo,
  CopyLinkPayload,
  CutLinkPayload,
  ILinkCardInfo,
  InjectCardInfoPayload,
  IRange,
  LinkDisplayType,
  LinkNode,
  RangeType,
  RemoveInjectedCardInfoPayload,
  RemoveLinkPayload,
  SaveLinkCardPayload,
  ShowLinkPastePanelPayload,
  SwitchDisplayTypePayload,
  UnwrapLinkCardPayload,
  UnwrapPureLinkPayload,
  UpdatePureLinkPayload,
} from './types';

export type {
  CardInfo,
  CopyLinkPayload,
  CutLinkPayload,
  ILinkCardInfo,
  InjectCardInfoPayload,
  IRange,
  LinkDisplayType,
  LinkNode,
  RangeType,
  RemoveInjectedCardInfoPayload,
  RemoveLinkPayload,
  SaveLinkCardPayload,
  ShowLinkPastePanelPayload,
  SwitchDisplayTypePayload,
  UnwrapLinkCardPayload,
  UnwrapPureLinkPayload,
  UpdatePureLinkPayload,
} from './types';

export const LINK_HIDE_EDITOR = Symbol('LINK_HIDE_EDITOR');

export const LINK_HIDE_EDITOR_AND_MOVE_NEXT_TEXT = Symbol('LINK_HIDE_EDITOR_AND_MOVE_NEXT_TEXT');

export const LINK_REMOVE = Symbol('LINK_REMOVE');

export const LINK_UPDATE = Symbol('LINK_UPDATE');

export const LINK_REMOVE_PURE = Symbol('LINK_REMOVE_PURE');

export const LINK_UPDATE_PURE = Symbol('LINK_UPDATE_PURE');

export const LINK_WRAP = Symbol('LINK_WRAP');

export const LINK_UNWRAP = Symbol('LINK_UNWRAP');

export const LINK_UNWRAP_CARD = Symbol('LINK_UNWRAP_CARD');

export const LINK_FOCUS_EDITOR = Symbol('LINK_FOCUS_EDITOR');

export const LINK_UNWRAP_PURE_LINK = Symbol('LINK_UNWRAP_PURE_LINK');

export const LINK_SWITCH_DISPLAYTYPE = Symbol('LINK_SWITCH_DISPLAYTYPE');

export const LINK_SWITCH_LINK_TO_CARD = Symbol('LINK_SWITCH_LINK_TO_CARD');

export const LINK_EDIT_LINK_CARD = Symbol('LINK_EDIT_LINK_CARD');

export const LINK_HOVER_LINK_CARD = Symbol('LINK_HOVER_LINK_CARD');

export const LINK_CLICK_PLACEHOLDER = Symbol('LINK_CLICK_PLACEHOLDER');

export const LINK_INJECT_CARDINFO = Symbol('LINK_INJECT_CARDINFO');

export const LINK_REMOVE_INJECTED_CARDINFO = Symbol('LINK_REMOVE_INJECTED_CARDINFO');

export const LINK_COPY_LINK = Symbol('LINK_COPY_LINK');

export const LINK_CUT_LINK = Symbol('LINK_CUT_LINK');

export const LINK_SAVE_LINK_CARD = Symbol('LINK_SAVE_LINK_CARD');

export const LINK_SHOW_LINK_PASRE_PANEL = Symbol('LINK_SHOW_LINK_PASRE_PANEL');

export const LINK_CUT_PURE = Symbol('LINK_CUT_PURE');

export const LINK_OPEN = Symbol('LINK_OPEN');

export function hideLinkEditor(): Action<{}> {
  return {
    type: LINK_HIDE_EDITOR,
    payload: {},
  };
}

export function hideLinkEditorAndMoveToNextText(): Action<{}> {
  return {
    type: LINK_HIDE_EDITOR_AND_MOVE_NEXT_TEXT,
    payload: {},
  };
}

export function wrapLink(range?: RangeType) {
  return {
    type: LINK_WRAP,
    payload: {
      range,
    },
  };
}

export function unwrapLink(): Action<{}> {
  return {
    type: LINK_UNWRAP,
    payload: {},
  };
}

export function unwrapLinkCard(node: Inline): Action<UnwrapLinkCardPayload> {
  return {
    type: LINK_UNWRAP_CARD,
    payload: { node },
  };
}

export function removeLink(node: Inline): Action<RemoveLinkPayload> {
  return {
    type: LINK_REMOVE,
    payload: { node },
  };
}

export function updateLink(link: Inline, text: string, href: string) {
  return {
    type: LINK_UPDATE,
    payload: {
      node: link,
      text,
      href,
    },
  };
}

export function updateLinkWithCardInfo(link: Inline, text: string, href: string, cardInfo: ILinkCardInfo) {
  return {
    type: LINK_UPDATE,
    payload: {
      node: link,
      text,
      href,
      cardInfo,
    },
  };
}

export function removePureLink(
  node: Text,
  offset: number,
  href: string,
): Action<{}> {
  return {
    type: LINK_REMOVE_PURE,
    payload: { node, offset, href },
  };
}

export function cutPureLink(
  node: Text,
  offset: number,
  href: string,
): Action<{}> {
  return {
    type: LINK_CUT_PURE,
    payload: { node, offset, href },
  };
}

export function updatePureLink(
  link: Text,
  offset: number,
  text: string,
  href: string,
  srcHref: string,
): Action<UpdatePureLinkPayload> {
  return {
    type: LINK_UPDATE_PURE,
    payload: {
      node: link,
      offset,
      text,
      href,
      srcHref,
    },
  };
}

export function updatePureLinkWithCardInfo(
  link: Text,
  offset: number,
  text: string,
  href: string,
  srcHref: string,
  cardInfo: ILinkCardInfo,
  needFetchLinkInfo?: boolean,
): Action<UpdatePureLinkPayload> {
  return {
    type: LINK_UPDATE_PURE,
    payload: {
      node: link,
      offset,
      text,
      href,
      srcHref,
      cardInfo,
      needFetchLinkInfo,
    },
  };
}

export function focusLinkEditor(): Action<{}> {
  return {
    type: LINK_FOCUS_EDITOR,
    payload: {},
  };
}

export function unwrapPureLink(
  node: Text,
  offset: number,
  href: string,
): Action<UnwrapPureLinkPayload> {
  return {
    type: LINK_UNWRAP_PURE_LINK,
    payload: {
      node,
      offset,
      href,
    },
  };
}

export function switchDisplayType(
  node: LinkNode,
  displayType: LinkDisplayType,
): Action<SwitchDisplayTypePayload> {
  return {
    type: LINK_SWITCH_DISPLAYTYPE,
    payload: {
      node,
      displayType,
    },
  };
}

export function editLinkCard() {
  return {
    type: LINK_EDIT_LINK_CARD,
    payload: {},
  };
}

export function hoverLinkCard() {
  return {
    type: LINK_HOVER_LINK_CARD,
    payload: {},
  };
}

export function handleLinkPlaceholder() {
  return {
    type: LINK_CLICK_PLACEHOLDER,
    payload: {},
  };
}

export function switchLink2Card(
  link: Inline,
  text: string,
  href: string,
  cardInfo: ILinkCardInfo,
) {
  return {
    type: LINK_SWITCH_LINK_TO_CARD,
    payload: {
      node: link,
      text,
      href,
      cardInfo,
    },
  };
}


export function injectCardInfo(
  node: Inline | Text,
  cardInfo: ILinkCardInfo,
  range?: IRange,
): Action<InjectCardInfoPayload> {
  return {
    type: LINK_INJECT_CARDINFO,
    payload: {
      node,
      cardInfo,
      range,
    },
  };
}

export function removeInjectCardInfo(
  node: Inline | Text,
): Action<RemoveInjectedCardInfoPayload> {
  return {
    type: LINK_REMOVE_INJECTED_CARDINFO,
    payload: {
      node,
    },
  };
}

export function copyLink(
  node: Inline,
): Action<CopyLinkPayload> {
  return {
    type: LINK_COPY_LINK,
    payload: {
      node,
    },
  };
}

export function cutLink(
  node: Inline,
): Action<CutLinkPayload> {
  return {
    type: LINK_CUT_LINK,
    payload: {
      node,
    },
  };
}

export function saveLinkCard(
  node: Inline,
  text: string,
  href: string,
): Action<SaveLinkCardPayload> {
  return {
    type: LINK_SAVE_LINK_CARD,
    payload: {
      node,
      text,
      href,
    },
  };
}

export function showLinkPastePanel(
  text: string,
  getLinkInfo?: (href: string) => Promise<CardInfo>,
): Action<ShowLinkPastePanelPayload> {
  return {
    type: LINK_SHOW_LINK_PASRE_PANEL,
    payload: {
      text,
      getLinkInfo,
    },
  };
}
